export default {
  props: {
    currentNodeId: {
      type: Number,
      default: 0
    }
  },
  computed: {
    createTopicUrl() {
      let url = '/topic/create'
      if (this.currentNodeId) {
        url += '?nodeId=' + this.currentNodeId
      }
      return url
    }
  }
}
